.sc-chart-size {
  display: flex !important;
  height: auto;
  width: auto !important; }
  .sc-chart-size:after {
    display: none; }
  .sc-chart-size > span {
    opacity: 1;
    position: relative;
    cursor: pointer;
    transition: scale 0.25s; }
  .sc-chart-size .cq-menu-btn {
    cursor: pointer;
    padding: 5px 0px; }
    .smartcharts-light .sc-chart-size .cq-menu-btn:hover {
      background: #e6e9e9; }
    .smartcharts-dark .sc-chart-size .cq-menu-btn:hover {
      background: #242828; }
