.ciq-period {
  padding: 0;
  line-height: 1.5em !important; }
  .ciq-period > .cq-menu-btn {
    padding: 0 5px; }
    .ciq-period > .cq-menu-btn > .bt-priod {
      display: block;
      height: auto;
      line-height: 2em;
      border-radius: 2px;
      box-sizing: border-box;
      text-align: center;
      font-size: 1em; }
      .ciq-period > .cq-menu-btn > .bt-priod .ic-priod {
        font-size: 1.2em;
        border-radius: 4px;
        padding: 3px 5px; }
  .ciq-period.stxMenuActive .bt-priod {
    font-weight: 500; }
  .smartcharts-light .ciq-period.stxMenuActive .ic-subtitle, .smartcharts-light
  .ciq-period.stxMenuActive .ic-priod span {
    color: #7f8397; }
  .smartcharts-dark .ciq-period.stxMenuActive .ic-subtitle, .smartcharts-dark
  .ciq-period.stxMenuActive .ic-priod span {
    color: #ffffff; }
  .ciq-period:after {
    display: none; }
  .ciq-period .unit_display {
    text-transform: capitalize; }
  .ciq-period .interval_display {
    margin: 0 5px 0 0; }
  .ciq-period .cq-menu-dropdown {
    width: 424px;
    height: 216px;
    font-size: 1.2em;
    font-weight: normal; }

.sc-interval {
  display: flex;
  padding: 24px;
  flex-direction: column;
  box-sizing: border-box;
  border-radius: 4px; }
  .smartcharts-light .sc-interval {
    background: #ffffff;
    color: #333333; }
  .smartcharts-dark .sc-interval {
    background: #151717;
    color: #c2c2c2; }
  .sc-interval__head {
    display: flex; }
    .sc-interval__head strong {
      font-size: 16px;
      font-weight: bold;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal; }
      .smartcharts-dark .sc-interval__head strong {
        color: #ffffff; }
  .sc-interval__info {
    display: none; }
  .sc-interval__content {
    display: flex;
    flex-wrap: wrap;
    padding-top: 16px; }
    .sc-interval__content .sc-interval__item {
      width: 23.9%;
      height: 32px;
      font-size: 14px;
      font-weight: normal;
      font-style: normal;
      line-height: 1.43;
      letter-spacing: normal;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 4px 8px 0px;
      cursor: pointer;
      border-radius: 4px;
      border: 1px solid transparent;
      box-sizing: border-box;
      position: relative; }
      .smartcharts-light .sc-interval__content .sc-interval__item:hover {
        background: #e6e9e9; }
      .smartcharts-dark .sc-interval__content .sc-interval__item:hover {
        background: #242828; }
      .sc-interval__content .sc-interval__item--active {
        background: transparent !important; }
        .smartcharts-light .sc-interval__content .sc-interval__item--active {
          color: #333333;
          border-color: #85acb0;
          font-weight: bold; }
        .smartcharts-dark .sc-interval__content .sc-interval__item--active {
          color: #ffffff;
          border-color: #85acb0;
          font-weight: bold; }
      .sc-interval__content .sc-interval__item--disabled {
        cursor: initial;
        background: transparent !important; }
        .sc-interval__content .sc-interval__item--disabled span {
          opacity: 0.32;
          pointer-events: none; }

/* Mobile Style */
.smartcharts-mobile .sc-interval {
  padding: 8px 10px 24px; }

.smartcharts-mobile .sc-interval__info {
  display: block; }

.smartcharts-mobile .sc-interval__content .sc-interval__item {
  width: 31.9% !important;
  margin: 0 4px 8px 0px; }
