.sc-download-menu .sc-dialog {
  width: 320px; }

.sc-download {
  padding: 24px;
  display: flex;
  border-radius: 4px; }
  .smartcharts-light .sc-download {
    background: #ffffff;
    color: #333333; }
  .smartcharts-dark .sc-download {
    background: #151717;
    color: #c2c2c2; }
  .sc-download__item {
    width: 88px;
    height: 88px;
    padding: 16px;
    box-sizing: border-box;
    border-radius: 4px;
    text-align: center;
    cursor: pointer;
    position: relative;
    margin-right: 8px; }
    .sc-download__item .ic-icon {
      width: 32px;
      height: 32px;
      margin: 0 auto 4px; }
    .smartcharts-light .sc-download__item:hover {
      background: #e6e9e9; }
    .smartcharts-dark .sc-download__item:hover {
      background: #242828; }
    .sc-download__item__label {
      font-size: 14px;
      font-weight: normal;
      font-style: normal;
      line-height: 1.43;
      letter-spacing: normal;
      display: block; }
    .sc-download__item--loading > .sc-download__item__label,
    .sc-download__item--loading > .ic-icon {
      opacity: 0.32; }

.smartcharts-mobile .sc-download {
  justify-content: space-around; }
  .smartcharts-mobile .sc-download__item {
    margin: 0px; }

.smartcharts-mobile .sc-download-menu .sc-dialog {
